<template>
  <div :class="['btn-top-wrap', isBottom ? 'is-bottom' : '']" ref="target">
    <transition>
      <button v-if="isShow" class="btn-top" @click="clickBtnTop"></button>
    </transition>
  </div>
</template>

<script>
import { animateScroll } from '@/assets/js/ui-common.js'

export default {
  name: 'BtnTop',
  props: {
  },
  data () {
    return {
      isShow: false,
      isBottom: false,
    }
  },
  methods: {
    // 스크롤 이벤트
    windowScroll: function (e) {
      if (window.scrollY >= window.innerHeight) {
        this.isShow = true
      } else {
        this.isShow = false
      }

      if (window.scrollY + window.innerHeight >= this.$refs.target.offsetTop) {
        this.isBottom = true
      } else {
        this.isBottom = false
      }
    },
    clickBtnTop: function () {
      animateScroll(0) // 최상단 이동
    }
  },
  created () {
    window.addEventListener('scroll', this.windowScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.windowScroll)
  },
}
</script>