<script>
export default {
  name: 'BreadcrumbsFilters',
  props: {
    sortbyList: Array
  },
  data () {
    return {
      isFixed: false,
      sortbyIndex: 0,
      sortbyOpened: false,
      sortbyHeight: 0,
      filtersOpened: false,
      filtersHeight: 0,
    }
  },
  methods: {
    // 정렬 클릭
    clickSortby: function () {
      this.sortbyOpened = !this.sortbyOpened
      this.filtersOpened = false
    },
    // 정렬 before-enter
    sortbyBeforeEnter: function (el) {
      el.style.display = 'block'
      el.style.height = ''
      this.sortbyHeight = el.clientHeight
      el.style.display = 'none'
      el.style.height = 0
    },
    // 정렬 enter
    sortbyEnter: function (el) {
      setTimeout(() => {
        el.style.height = this.sortbyHeight + 'px'
      }, 0)
    },
    // 정렬 leave
    sortbyLeave: function (el) {
      setTimeout(() => {
        el.style.height = '0px'
      }, 0)
    },

    // 필터 클릭
    clickFilters: function () {
      this.filtersOpened = !this.filtersOpened
      this.sortbyOpened = false
    },
    // 필터 before-enter
    filtersBeforeEnter: function (el) {
      el.style.display = 'block'
      el.style.height = ''
      el.firstChild.style.height = ''
      this.filtersHeight = el.firstChild.clientHeight
      el.style.display = 'none'
      el.style.height = 0
      el.firstChild.style.height = 0
    },
    // 필터 enter
    filtersEnter: function (el) {
      setTimeout(() => {
        el.style.height = this.filtersHeight + 'px'
        el.firstChild.style.height = this.filtersHeight + 'px'
      }, 0)
    },
    // 필터 leave
    filtersLeave: function (el) {
      setTimeout(() => {
        el.style.height = '0px'
        el.firstChild.style.height = '0px'
      }, 0)
    },

    // 스크롤 반응
    windowScroll: function (e) {
      // 87 fixed-header height
      if (window.scrollY + 87 >= this.$refs.target.offsetTop) {
        this.isFixed = true
      } else {
        this.isFixed = false
      }
    },
    // 바깥쪽 클릭
    outsideClick: function (e) {
      this.sortbyOpened = false
      this.filtersOpened = false
    }
  },
  created () {
    window.addEventListener('scroll', this.windowScroll)
    document.addEventListener('click', this.outsideClick)
  },
  destroyed () {
    window.removeEventListener('scroll', this.windowScroll)
    document.removeEventListener('click', this.outsideClick)
  },
}
</script>

